/**
* 1. The Ink Page
* 2. About page
*/

/* 1. The Ink Page */
body.postid-212 {
    #active-post {
        display: none;
        position: fixed;
        width: 50%;
        margin-left: 10%;
        height: 100vh;
        overflow: hidden;
        #img-outer {
            width: 80%;
            height: 80%;
            position: relative;
            top: 50%;
            left: 50%;
            transform: translate(-50% , -50%);
            img {
                width: auto;
                max-height: 100%;
                position: relative;
                display: block;
                margin: auto;
                top: 50%;
                transform: translateY(-50%);
            }
            #controls {
                position: absolute;
                bottom: 10%;
            }
            #share {
                position: absolute;
                text-align: center;
                bottom: 0;
                transform: translate(165px, 100%);
                a {
                    display: inline-block;
                    margin: 10px 30px 0 0;
                    text-align: center;
                    color: $secondary;
                    height: 50px;
                    line-height: 50px;
                }
            }
        }
        #active-post-blur {
            position: absolute;
            width: 100%;
            height: 100%;
            background-position: center;
            background-size: cover;
            background-repeat: no-repeat;
            filter: blur(6px) saturate(45%);
            opacity: 0.5;
        }
    }
    .page-title {
        position: relative;
        height: 300px;
        text-align: center;
        width: 40%;
        left: 60%;
        span {
            position: relative;
            top: 50%;
            transform: translateY(-50%);
            font-size: 70px;
            color: $secondary;
            display: block;
        }
    }
    
    #gallery-posts-outer {
        position: relative;
        width: 40% !important;
        left: 60%;
        opacity: 0;
        transition: opacity 0.5s;
        .gallery-post {
            position: relative;
            width: 32.8%;
            padding: 10px;
            cursor: pointer;
            display: inline-block;
            &.active-post {
                img {
                    padding: 5px;
                    border: 1px solid $primary;
                    filter: saturate(100%)
                }
            }
            img {
                transition: border 0.5s;
                filter: saturate(30%)
            }
        }
    }
}



/**
* 2. Care page
*/
main#care-page {
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    #care-back-overlay {
        height: 100%;
        width: 100%;
        display: block;
        position: absolute;
        top: 0;
        background: rgba(0,0,0,0.8);
    }
    #post-wrapper {
        width: 80%;
        margin-left: 10%;
        height: 100vh;
        position: relative;
        .post-outer {
            width: 100%;
            height: 50%;
            background: rgba(255,255,255,0.1);
            border-radius: 30px;
            transform: translateY(-50%);
            top: 50%;
            position: absolute;
            display: none;
            .info-outer {
                width: 50%;
                float: left;
                position: relative;
                height: 100%;
                .info {
                    color: $secondary;
                    .name {
                        font-size: 60px;
                        transform: translateY(-50%);
                        font-weight: 900;
                        margin-left: 50px;
                    }
                    .about-text {
                        padding: 0 50px 0 50px;
                    }
                }
            }
            .prod-pic {
                width: 50%;
                float: left;
                position: relative;
                height: 100%;
                overflow: hidden;
                .prod-back {
                    height: 100%;
                    width: 100%;
                    position: absolute;
                    border-radius: 0 30px 30px 0;
                    filter: saturate(50%);
                    background-repeat: no-repeat;
                    background-position: center;
                    background-size: cover;
                }
                .prod-pic-outer {
                    width: 350px;
                    height: 350px;
                    background: rgba(0,0,0,0.8);
                    border-radius: 50%;
                    top: 50%;
                    position: relative;
                    display: block;
                    left: 50%;
                    transform: translate(-50%,-50%);
                    img {
                        position: relative;
                        top: 50%;
                        left: 50%;
                        transform: translate(-50% , -50%);
                    }
                }
            }
        }
    }
}